<template>
  <div>
    <!--添加面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--卡片区域-->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddRoleDialog"
            >添加角色</el-button
          >
        </el-col>
      </el-row>
      <el-table :data="rolesList" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!--渲染一级权限-->
            <el-row
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
              :class="['tagbottom', i1 === 0 ? 'tagtop' : '', 'vCenter']"
            >
              <el-col :span="5">
                <el-tag closable @close="delRightsById(scope.row, item1.id)">{{
                  item1.authName
                }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <!--渲染二级权限-->
                <el-row
                  :class="[i2 === 0 ? '' : 'tagtop', 'vCenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="delRightsById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--渲染三级权限-->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="delRightsById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEditRoleDialog(scope.row.id)"
              >编辑</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteRoleById(scope.row.id)"
              >删除</el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="setRightsDialog(scope.row)"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--
          分配权限对话框
      -->
      <el-dialog
        title="权限分配"
        :visible.sync="setRightsDialogVisible"
        width="50%"
        @close="rightsDialogClosed"
      >
        <el-tree
          :data="rightList"
          :props="treeProps"
          show-checkbox
          node-key="id"
          default-expand-all
          :default-checked-keys="idKeys"
          ref="treeRef"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRightsDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="allotRights">确 定</el-button>
        </span>
      </el-dialog>
      <!--添加角色对话框-->
      <el-dialog
        title="添加角色"
        :visible.sync="addRoleDialogVisible"
        width="50%"
        @close="addRoleDialogClosed"
      >
        <el-form
          :model="addRoleForm"
          :rules="addRoleFormRules"
          ref="addRoleFormRef"
          label-width="100px"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="addRoleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="addRoleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addRoleDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addRole">确 定</el-button>
        </span>
      </el-dialog>
      <!--修改角色对话框-->

      <el-dialog
        title="修改角色"
        :visible.sync="editRoleDialogVisible"
        width="50%"
        @close='editRoleClose'
      >
        <el-form
          :model="editRoleForm"
          :rules="editRoleFormRules"
          ref="editRoleFormRef"
          label-width="100px"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="editRoleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="editRoleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editRoleDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editRole">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  created() {
    this.getRolesList();
  },
  data() {
    return {
      //保存get请求的角色列表的数据
      rolesList: [],
      //权限分配是否可见
      setRightsDialogVisible: false,
      //保存所有权限列表数据
      rightList: [],
      treeProps: {
        label: "authName",
        children: "children",
      },
      idKeys: [],
      roleId: "",
      addRoleDialogVisible: false,
      addRoleForm: {
        roleName: "",
        roleDesc: "",
      },
      addRoleFormRules: {
        roleName: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
        ],
        roleDesc: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
        ],
      },
      editRoleDialogVisible: false,
      editRoleForm:{
        roleName: "",
        roleDesc: "",
        roleId:0
      },
      editRoleFormRules: {
        roleName: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
        ],
        roleDesc: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    async getRolesList() {
      //通过get方式请求角色列表的数据
      const { data: res } = await this.$http.get("roles");
      //如果状态码不等于200就提示获取角色列表数据失败
      if (res.meta.status !== 200) {
        return this.$Message.error("获取角色列表数据失败");
      }
      //将获取到的数据保存到rolesList
      this.rolesList = res.data;
    },
    //根据id删除角色权限
    async delRightsById(role, rightsId) {
      const result = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      if (result !== "confirm") {
        return this.$Message.info("取消了删除");
      }
      //发起删除请求
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightsId}`
      );
      if (res.meta.status !== 200) {
        return this.$Message.error("删除角色权限失败");
      }
      //将当前有的权限赋值给当前角色
      role.children = res.data;
    },
    async setRightsDialog(role) {
      this.roleId = role.id;
      //获取所有权限列表数据
      const { data: res } = await this.$http.get("rights/tree");
      //将获取到的权限列表数据保存到rightList
      this.rightList = res.data;
      //将当前角色的权限设置为默认权限
      this.getLeafKey(role, this.idKeys);

      this.setRightsDialogVisible = true;
    },
    getLeafKey(node, arr) {
      //如果当前节点不包含children，则为三级节点
      if (!node.children) {
        //将节点Id存入节点数组
        arr.push(node.id);
      }
      //如果不包含便遍历当前节点的所有children
      if (node.children) {
        for (var item of node.children) {
          this.getLeafKey(item, arr);
        }
      }
    },
    rightsDialogClosed() {
      //当权限对话框关闭以后将Id数组重置为空
      this.idKeys = [];
    },
    //当点击确定按钮时，为角色分配权限
    async allotRights() {
      //获取选中和半选中的权限的id
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
      //将获取的id数组转换为字符串
      const keyStr = keys.join(",");
      //发起为角色分配权限的请求
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: keyStr }
      );
      if (res.meta.status !== 200) {
        return this.$Message.error("为角色分配权限失败");
      }
      //刷新权限列表
      this.getRolesList();
      //将对话框关闭
      this.setRightsDialogVisible = false;
    },
    showAddRoleDialog() {
      this.addRoleDialogVisible = true;
    },
    async addRole() {
      const { data: res } = await this.$http.post("roles", this.addRoleForm);
      if (res.meta.status !== 201) {
        return this.$Message({
          message: "添加角色失败！",
          type: "error",
        });
      }
      this.getRolesList();

      this.addRoleDialogVisible = false;
    },
    addRoleDialogClosed() {
      this.$refs.addRoleFormRef.resetFields();
    },
    async deleteRoleById(id) {
      const result = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      if (result !== "confirm") {
        return this.$Message({
          message: "取消了删除",
          type: "info",
        });
      }
      const { data: res } = await this.$http.delete(`roles/${id}`);
      if (res.meta.status !== 200) {
        return this.$Message({
          message: "删除角色失败！",
          type: "error",
        });
      }
      this.getRolesList();
    },
    async editRole() {
     const {data:res} = await this.$http.put(`roles/${this.editRoleForm.roleId}`,
       { roleName: this.editRoleForm.roleName,
         roleDesc: this.editRoleForm.roleDesc
       }
     )
     console.log(res)
     if(res.meta.status !== 200){
        return this.$Message({
          message: "修改角色失败！",
          type: "error",
        })
     }
     this.getRolesList()
     this.editRoleDialogVisible = false;
    },
    async showEditRoleDialog(id) {
      const {data:res} =await this.$http.get(`roles/${id}`)
      if(res.meta.status !== 200){
         return this.$Message({
          message: "查询角色失败！",
          type: "error",
        });
      }
     this.editRoleForm.roleId = res.data.roleId
      this.editRoleDialogVisible = true;
    },
    editRoleClose(){
       this.$refs.editRoleFormRef.resetFields();
    }
  },
};
</script>
<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.tagtop {
  border-top: 1px solid #eee;
}
.tagbottom {
  border-bottom: 1px solid #eee;
}
.vCenter {
  display: flex;
  align-items: center;
}
</style>
