<template>
  <div>
    <!--添加面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--卡片区域-->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            class="input-with-select"
            v-model="infoList.query"
            clearable
            @clear="getUserData"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserData"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="3"
          ><el-button type="primary" @click="dialogVisible = true"
            >添加用户</el-button
          ></el-col
        >
      </el-row>
      <!--表格区域-->
      <el-table :data="userList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="username" label="姓名"> </el-table-column>
        <el-table-column prop="email" label="邮箱"> </el-table-column>
        <el-table-column prop="mobile" label="电话"> </el-table-column>
        <el-table-column prop="role_name" label="角色"> </el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChange(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editDialog(scope.row.id)"
            >
            </el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="delUserById(scope.row.id)"
            ></el-button>
            <el-tooltip
              class="item"
              effect="dark"
              content="这是设置"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRoles(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="infoList.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="infoList.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--对话框区域-->
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="50%"
      @close="resetFormData"
    >
      <el-form
        :model="formData"
        :rules="formRules"
        ref="formRef"
        label-width="70px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="formData.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="formData.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="formData.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="formData.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>
    <!--修改用户对话框-->
    <el-dialog
      title="修改用户"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="resetEditForm"
    >
      <el-form
        :model="editForm"
        :rules="editRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="用户名">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUser">确 定</el-button>
      </span>
    </el-dialog>
    <!--分配角色对话框-->
    <el-dialog
      title="分配角色"
      :visible.sync="setRolesDialogVisible"
      width="50%"
      @close= 'setRolesDialogClosed'
    >
      <div>
        <p>当前的用户：{{ userInfo.username }}</p>
        <p>当前的角色：{{ userInfo.role_name }}</p>
        <p>
          分配新角色:
          <el-select v-model="selectedRoleId" placeholder="请选择">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRolesDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRole">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
//验证邮箱的回调函数
var checkEmail = (rule, value, callback) => {
  const regEmail = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
  //判断是否验证通过若验证通过，则通过调用callback函数退出去
  if (regEmail.test(value)) {
    callback();
  }
  //返回一个错误信息
  callback(new Error("请输入合法的邮箱"));
};
//验证手机的回调函数
var checkMobile = (rule, value, callback) => {
  const regMobile = /^[1][3,4,5,7,8][0-9]{9}$/;
  //判断是否验证通过若验证通过，则通过调用callback函数退出去
  if (regMobile.test(value)) {
    callback();
  }
  //返回一个错误信息
  callback(new Error("请输入合法的手机号"));
};
export default {
  //当组件加载完以后就调用请求user列表数据
  created() {
    this.getUserData();
  },
  data() {
    return {
      setRolesDialogVisible: false,
      infoList: {
        query: "", //查询字符串
        pagenum: 1,
        pagesize: 2,
      },
      userList: [],
      total: 0,
      dialogVisible: false,
      formData: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      formRules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 10 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入登录密码", trigger: "blur" },
          {
            min: 5,
            max: 16,
            message: "长度在 5 到 16 个字符",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输手机号", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
      editDialogVisible: false,
      //存储修改用户的数据
      editForm: {},
      //验证规则
      editRules: {
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输手机号", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
      userInfo: {},
      roleList: [],
      selectedRoleId: "",
    };
  },
  methods: {
    async getUserData() {
      const { data: res } = await this.$http.get("users", {
        params: this.infoList,
      });

      //若状态码不等于200则获取失败，弹出错误信息
      if (res.meta.status !== 200)
        return this.$message.error("获取用户列表数据失败");
      //将获取到的total保存到total中,user保存到userList中
      this.total = res.data.total;
      this.userList = res.data.users;
    },
    //每一页显示数量改变时
    handleSizeChange(val) {
      this.infoList.pagesize = val;
      //每页显示多少数据改变时需要重新调用请求user列表数据
      this.getUserData();
    },
    //当前页面改变时
    handleCurrentChange(val) {
      this.infoList.pagenum = val;
      //页码值改变时也需要重新调用user列表数据
      this.getUserData();
    },
    //当用户状态改变时同步到数据库
    async userStateChange(infoList) {
      //当用户状态改变的时候，将改变的状态保存到数据库中，这样下次刷新的时候还是改变的状态
      const { data: res } = await this.$http.put(
        `users/${infoList.id}/state/${infoList.mg_state}`
      );
      if (res.meta.status !== 200) {
        //若更新失败则将状态切换为原状态
        this.infoList.mg_state = !this.infoList.status;
        return this.$message.error("更新用户状态失败");
      }
    },
    //重置表单函数，当对话框关闭以后便将表单重置
    resetFormData() {
      this.$refs.formRef.resetFields();
    },
    //添加用户函数
    addUser() {
      //先验证要提交的表单数据是否符合规则，若符合则提交至数据库
      this.$refs.formRef.validate(async (valid) => {
        //若验证失败，则不添加
        if (!valid) {
          return;
        }
        const { data: res } = await this.$http.post("users", this.formData);
        if (res.meta.status !== 201) {
          return this.$Message({
          message: "添加用户失败！",
          type: "error",
        });
        }
        //添加用户成功以后将对话框关闭
        this.dialogVisible = false;
        //将数据显示到用户列表
        this.getUserData();
      });
    },
    async editDialog(id) {
      //当点击修改按钮时显示修改对话框
      this.editDialogVisible = true;
      //根据用户ID查询用户信息
      const { data: res } = await this.$http.get("users/" + id);
      if (res.meta.status !== 200) {
        return this.$Message({
          message: "查询用户数据失败！",
          type: "error",
        });
      }
      //将查询到的数据保存到editForm中
      this.editForm = res;
    },
    //重置修改表单函数
    resetEditForm() {
      this.$refs.editFormRef.resetFields();
    },
    editUser() {
      //首先验证要修改的数据
      this.$refs.editFormRef.validate(async (valid) => {
        //如果验证数据失败就退出函数
        if (!valid) {
          return;
        }
        console.log(this.editForm);
        const { data: res } = await this.$http.put(
          "users/" + this.editForm.data.id,
          {
            email: this.editForm.data.email,
            mobile: this.editForm.data.mobile,
          }
        );
        if (res.meta.status !== 200) {
          return this.$Message({
          message: "修改用户数据失败！",
          type: "error",
        });
        }
        //获取修改的数据，并渲染到数据列表
        this.getUserData();
        //将对话框关闭
        this.editDialogVisible = false;
      });
    },
    async delUserById(id) {
      const result = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      if (result !== "confirm") {
         return this.$Message({
          message: "已取消删除！",
          type: "info",
        });
      }
      //发起删除用户请求
      const { data: res } = await this.$http.delete("users/" + id);
      //如果状态码不等于200，则删除失败
      if (res.meta.status !== 200) {
        return this.$Message({
          message: "删除用户失败！",
          type: "error",
        });
      }
      //更新用户列表
      this.getUserData();
    },
    //分配角色
    async setRoles(user) {
      //将用户信息保存到userInfo
      this.userInfo = user;
      //获取角色列表数据
      const { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200) {
         return this.$Message({
          message: "获取角色列表失败！",
          type: "error",
        });
      }
      //将角色列表数据保存到roleList
      this.roleList = res.data;
      //打开分配角色对话框
      this.setRolesDialogVisible = true;
    },
    async allotRole() {
      //如果没有选中角色,则退出函数
      if (!this.selectedRoleId) {
        return;
      }
      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        { rid: this.selectedRoleId }
      );
      if(res.meta.status !== 200){
        return this.$Message.error('分配用户角色失败')
      }
      //刷新用户列表
      this.getUserData()
      //退出弹框
      this.setRolesDialogVisible = false
    },
    //当修改对话框关闭时，将selectedRoleId置为'',userInfo置为空
    setRolesDialogClosed(){
        this.selectedRoleId = ''
        this.userInfo = ''
    }
  },
};
</script>
<style lang="less" scoped>
.el-pagination {
  margin-top: 15px;
}
</style>